
import { Toast } from 'antd-mobile';
/**
 * @desc   描述
 * @date   2018-04-28
 * @author luozhou
 * @param  {[Number]}   px [设计图实际像素大小]
 * @return {[Number]}      [返回一个计算后的大小]
 */
const initWidth = 750;
const currentWidth = window.innerWidth;
export const MathPx = (px)=>{
	return px/(initWidth/currentWidth)
}
/**
 * @desc   描述
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     [请求路径]
 * @param  {[Object]}   params  [请求类型]
 * @param  {[Function]} resolve [成功回调函数]
 */
const post = (url,params,resolve)=>{
	let body = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'post',
	    method:'post',
	    body,
	    headers:{
	        'Accept': 'application/x-www-form-urlencoded',
	        'Content-Type': 'application/x-www-form-urlencoded',
	    }
	}).then((response)=>{
	    if( !response.ok ) console.log('请求失败');
	    return response.json();
	}).then((json)=>{
		if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}
/**
 * @desc   描述
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     [请求路径]
 * @param  {[Function]} resolve [成功回调函数]
 */
const get = (url,resolve)=>{
	fetch(url,{
	    type:'get',
	    method:'get'
	}).then((response)=>{
	    if( !response.ok ) console.log('请求失败');
	    return response.json();
	}).then((json)=>{
	    if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}
/**
 * @desc   描述
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     [请求路径]
 * @param  {[Object]}   params  [请求类型]
 * @param  {[Function]} resolve [成功回调函数]
 */
const file = (url,params,resolve)=>{
	let body = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'file',
	    method:'post',
	    body
	}).then((response)=>{
	    if( !response.ok ) console.log('请求失败');
	    return response.json();
	}).then((json)=>{
	    if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}

export const Ajax = {
	post,
	get,
	file
}


