import React, { Component } from 'react';
// redux
import { createStore } from 'redux';
import { Provider, connect } from 'react-redux';
// router
import { Link } from 'react-router-dom';
// react component state update
import addons from 'react-addons-update';
import update from 'react-update';
// dirty checking
import PropTypes from 'prop-types';
// antd-mobile
import { WhiteSpace, WingBlank, NavBar, Icon, Carousel, Button } from 'antd-mobile';

import { createForm } from 'rc-form';
import { MathPx, Ajax } from './utils/global';
import { config } from './utils/config';

import logo from './images/logo.png';
import account from './images/account.svg';
import Category from './images/Category.svg';
import close from './images/close.svg';


import './css/iconfont/iconfont.css';

class component extends Component {
    constructor(props){
        super(props)
        this.update = update.bind(this);
        this.state={
            Modal:{
                visMenu:false
            },
            CarouselData:[],
            menuList:[],
            ProductDescList:[],
            solutionList:[]
        }
    }
    // 
    async componentDidMount() {
        await this.initMenuList();
        await this.initBannerList();
        await this.initProductDescList();
        await this.initSolutionList();
    }
    /**
     * @desc   初始化菜单列表数据
     * @date   2018-05-02
     * @author luozhou
     * @return {[Promise]}
     */
    initMenuList(){
        const _this = this;
        return new Promise((resolve,reject)=>{
            Ajax.get(config.App.urls.menuList,(response)=>{
                _this.update('set',addons(_this.state,{
                    menuList:{
                        $set:response    
                    }
                }))
                resolve();
            })
        })
    }
    /**
     * @desc   初始化轮播图数据
     * @date   2018-05-02
     * @author luozhou
     * @return {[Promise]}
     */
    initBannerList(){
        const _this = this;
        return new Promise((resolve,reject)=>{
            Ajax.get(config.App.urls.bannerList,(response)=>{
                _this.update('set',addons(_this.state,{
                    CarouselData:{
                        $set:response    
                    }
                }))
                resolve();
            })
        })
    }
    /**
     * @desc   初始化产品优势
     * @date   2018-05-02
     * @author luozhou
     * @return {[Promise]}
     */
    initProductDescList(){
        const _this = this;
        return new Promise((resolve,reject)=>{
            Ajax.get(config.App.urls.productDescList,(response)=>{
                _this.update('set',addons(_this.state,{
                    ProductDescList:{
                        $set:response    
                    }
                }))
                resolve();
            })
        })
    }
    /**
     * @desc   初始化解决方案
     * @date   2018-05-02
     * @author luozhou
     */
    initSolutionList(){
        const _this = this;
        return new Promise((resolve,reject)=>{
            Ajax.get(config.App.urls.solutionList,(response)=>{
                _this.update('set',addons(_this.state,{
                    solutionList:{
                        $set:response    
                    }
                }))
                resolve();
            })
        })
    }
    render() {
        const _this = this;
        const state = _this.state;
        const update = _this.update;
        return (
            <div>
                <NavBar
                    style={{ backgroundColor: '#2a303c', height: MathPx(100) }}
                    mode="light"
                    leftContent={<Link style={{'color':'#fff'}} to="/"><img height={MathPx(60)} src={logo} alt="智卓安全"/></Link>}
                    rightContent={[
                        <img key="0" height={MathPx(50)} src={account} alt="用户信息"/>,
                        <img key="1" height={MathPx(50)} src={ state.Modal.visMenu ? close : Category} alt="菜单" onClick={()=>{
                            update('set',addons(state,{
                                Modal:{
                                    visMenu:{$set:!state.Modal.visMenu}     
                                }
                            }))
                        }} style={{"marginLeft":MathPx(46)}} />
                    ]}></NavBar>

                <div style={ state.Modal.visMenu ? {height:window.innerHeight-MathPx(100)} : {height:0}} className="menu">
                    <ul className="menuList">
                    {
                        state.menuList.map((el,index)=>(
                            <li key={el.id} className="items" style={{paddingLeft:MathPx(38),height:MathPx(100),lineHeight:MathPx(100)+'px'}}>
                                <Link style={{'color':'#fff'}} to={el.url}>{el.title}</Link>
                            </li>
                        ))
                    }
                    </ul>
                    <WhiteSpace />
                    <WhiteSpace />
                    <WingBlank size="md">
                        <Button type="primary">免费注册，即享免费套餐</Button>
                    </WingBlank>
                </div>
                
                <Carousel
                    autoplay={false}
                    infinite
                    style={{ height: MathPx(500) }}
                    beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    afterChange={index => console.log('slide to', index)}>
                    {
                        state.CarouselData.map(item => (
                            <a key={item.id}
                               href="javascript:;"
                               style={{ display: 'inline-block', width: '100%', overflow:'hidden' }}>
                                <img src={item.img_src}
                                    alt="轮播图"
                                    style={{ height:MathPx(500), verticalAlign: 'top', transform:'translateX(-30%)', position: 'relative'}}
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                        update('set',addons(state,{
                                            imgHeight:{$set:"auto"}
                                        }))
                                }}/>
                            </a>
                        ))
                    }
                </Carousel>

                <WhiteSpace />

                <WingBlank size="md">
                    <h4 style={{fontSize:MathPx(48),marginTop:MathPx(100)}} className="app-title">
                        产品优势
                    </h4>
                    <h6 style={{fontSize:MathPx(24),marginTop:MathPx(30),marginBottom:MathPx(80)}} className="app-sub-title">
                        企业级标准研发产品，保障用户的利益最大化
                    </h6>
                    <ul className="productDescList">
                        {
                            state.ProductDescList.map((el,index)=>(
                                <li key={index} className="items" style={{padding:MathPx(35),marginBottom:MathPx(20)}}>
                                    <h5 className="title" style={{fontSize:MathPx(34)}}>
                                        <span style={{fontSize:MathPx(50),marginRight:MathPx(26),top:MathPx(14)}} className={"icon iconfont "+el.icon}></span>
                                        {el.title}
                                    </h5>
                                    <p className="detail" style={{fontSize:MathPx(24),lineHeight:MathPx(44)+'px'}}>
                                        {el.desc}
                                    </p>  
                                </li>
                            ))
                        }
                    </ul>
                </WingBlank>
                <WhiteSpace />
                <WhiteSpace />
                <div className="solution" style={{paddingLeft:MathPx(72),paddingRight:MathPx(72)}}>
                    <h4 style={{fontSize:MathPx(48),marginTop:MathPx(100)}} className="app-title">
                        解决方案
                    </h4>
                    <h6 style={{fontSize:MathPx(24),marginTop:MathPx(30),marginBottom:MathPx(80)}} className="app-sub-title">
                        洞悉行业内的需求痛点，灵活定制解决方案
                    </h6>
                    <ul className="solutionList">
                        {
                            state.solutionList.map((el,index)=>(
                               <li key={index} style={{height:MathPx(970)}} className="items">
                                    <img src={el.img_src} alt="解决方案"/>
                                    <div className="title">
                                        <p><span style={{fontSize:MathPx(50)}} className={"icon iconfont "+el.icon}></span></p>
                                        <h3 style={{fontSize:MathPx(48)}}>{el.title}</h3>
                                    </div>
                                </li> 
                            ))
                        }
                    </ul>
                </div>
                
                <WhiteSpace />

                
            </div>
        );
    }
}

const App = createForm()(component);
export default App;



